.hac-header{height:4rem;border-bottom:1px solid #ddd;display:flex;align-items:center;justify-content:center}.hac-header span:first-child{margin-right:1rem}.hac-header span:last-child{margin-left:1rem}.hac-header span{font-weight:700;font-size:1.75rem}

:root{--bg-color: #fcfcfc;--font-color: #121b19;--logo-inner-bg-color: #fcfcfc;--logo-border-color: #121b19;--logo-head-color: #121b19;--logo-head-border: #fcfcfc;--logo-body-color: #fcfcfc;--logo-body-border: #121b19;--logo-eye-color: #fcfcfc}[data-theme="light"]{--bg-color: #fcfcfc;--font-color: #121b19;--logo-inner-bg-color: #fcfcfc;--logo-border-color: #121b19;--logo-head-color: #121b19;--logo-head-border: #fcfcfc;--logo-body-color: #fcfcfc;--logo-body-border: #121b19;--logo-eye-color: #fcfcfc}[data-theme="dark"]{--bg-color: #121b19;--font-color: #fcfcfc;--logo-inner-bg-color: #121b19;--logo-border-color: #fcfcfc;--logo-head-color: #fcfcfc;--logo-head-border: #121b19;--logo-body-color: #121b19;--logo-body-border: #fcfcfc;--logo-eye-color: #121b19}body{background-color:#fcfcfc;background-color:var(--bg-color);color:#121b19;color:var(--font-color)}.hac-logo{width:2.5rem;height:2.5rem;position:relative;overflow:hidden;background-color:#fcfcfc;background-color:var(--logo-inner-bg-color);border:2px solid #121b19;border:2px solid var(--logo-border-color);border-radius:100%}.hac-logo-head{width:1.125rem;height:1.125rem;position:absolute;left:.625rem;top:.375rem;background-color:#121b19;background-color:var(--logo-head-color);border:1px solid #fcfcfc;border:1px solid var(--logo-head-border);border-radius:100%;z-index:25}.hac-logo-neck{width:.625rem;height:.625rem;position:absolute;left:.9625rem;top:1.15625rem;background-color:#121b19;background-color:var(--logo-head-color);border-radius:90% 90% 100% 100%;z-index:24}.hac-logo-eye{width:.21875rem;height:.21875rem;position:absolute;left:.25rem;top:.4375rem;background-color:#fcfcfc;background-color:var(--logo-eye-color);border-radius:100%;z-index:25}.hac-logo-eye::before{width:.21875rem;height:.21875rem;position:absolute;left:.40625rem;content:"";background-color:#fcfcfc;background-color:var(--logo-eye-color);border-radius:100%;z-index:25}.hac-logo-body{width:1.25rem;height:1.875rem;position:absolute;left:.59375rem;top:1.25rem;background-color:#fcfcfc;background-color:var(--logo-body-color);border:1px solid #121b19;border:1px solid var(--logo-body-border);border-radius:100%;z-index:23}.hac-logo:hover .hac-logo-eye{animation-name:movingeyeleft;animation-duration:1.8s;animation-direction:normal;animation-iteration-count:infinite}.hac-logo:hover .hac-logo-eye::before{animation-name:movingeyeright;animation-duration:1.8s;animation-direction:normal;animation-iteration-count:infinite;animation-fill-mode:both}@keyframes movingeyeleft{0%{left:.25rem}25%{left:.15625rem}50%{left:.25rem}75%{left:.40625rem}100%{left:.25rem}}@keyframes movingeyeright{0%{left:.40625rem}25%{left:.3125rem}50%{left:.40625rem}75%{left:.375rem}100%{left:.40625rem}}

